.Container {
    text-align: center;
    background-color: #0c1a11;
    width: auto;
    height: auto;
    box-sizing:content-box;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  
  html {
    background-color: #0c1a11;
  }
  
  .BasketballBoard {
    background-color: #1e2e22;
    width: 100%;
    height: auto;
    margin-bottom: 1vmin;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Control {
    background-color: #1e2e22;
    width: 100%;
    height: auto;
    margin-bottom: 1vmin;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Input {
    background-color: #1e2e22;
    padding: 1vmin;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    color: white;
  }
  
  .Selects {
    display: flex;
    justify-content: space-evenly;
    width: 90vw;
  }
  
  .InfoDump {
    overflow:auto;
    background-color:white;
    color: black;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border: .2vmin solid black;
    height: 8vw;
    margin: 1vw;
    width: 60vw;
    white-space: pre;
  }
  
  .GameInfo {
    width: 100%;
    height: auto;
    background-color: grey;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 1vmin;
    padding-bottom: 1vmin;
  }
  
  .GameName {
    font-size: 2vmin;
    font-weight: bold;
    width: auto;
    background-color: white;
    border: .3vmin solid black;
    padding: .2vmin;
    margin-bottom: .3vmin;
  }
  
  .ScoresTimes {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .ScoreCounter {
    border: .3vmin solid black;  
    background-color: white;
    font-size: 3vmin;
    font-weight: bold;
    padding: .3vmin;
    margin-left: 1vmin;
    margin-right: 1vmin;
  }
  
  .QuarterCount {
    border: .3vmin black;  
    border-style: solid;
    background-color: white;
    font-size: 3vmin;
    font-weight: bold;
    padding: .3vmin;
  }
  
  .QuarterCount:hover {
    cursor:pointer;
  }
  
  .Rosters {
    width: 100%;
    height: auto;
    background-color: #1d1e2c;
    display: flex;
    justify-content: space-evenly;
  }
  
  .RosterHeader {
    background-color: #ffffff;
    width: 100%;
  }
  
  .HomeRoster {
    width: 50%;
    height: auto;
    border: 1px solid black;
  }
  
  .AwayRoster {
    width: 50%;
    height: auto;
    border: 1px solid black;
  }
  
  /* The switch - the box around the slider */
  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }